<template>
  <div class="innerDraw">
    <div class="wrap">
      <canvas
        id="canvas"
        width="800"
        height="600"
        @mousedown="canvasDown($event)"
        @mouseup="canvasUp($event)"
        @mousemove="canvasMove($event)"
        @touchstart="canvasDown($event)"
        @touchend="canvasUp($event)"
        @touchmove="canvasMove($event)"
      >
      </canvas>
    </div>
  </div>
</template>


<script>

  export default {

    data () {
      return {
        config: {
          lineWidth: 1,
          lineColor: '#f2849e',
          shadowBlur: 2
        }
      };
    },
    created() {
      this.pathArray = [];
    },
    mounted () {
      const canvas = document.querySelector('#canvas');
      this.context = canvas.getContext('2d');
      this.setCanvasStyle();
      this.connectEvent();
    },
    methods: {
      canvasMove (e) {
        if (this.canvasMoveUse) {
          console.log('canvasMove');
          const t = e.target;
          let canvasX;
          let canvasY;
          canvasX = e.clientX - t.parentNode.offsetLeft;
          canvasY = e.clientY - t.parentNode.offsetTop;
          this.context.lineTo(canvasX, canvasY);
          this.context.stroke();
          this.pathArray.push({
            x: canvasX,
            y: canvasY
          });
        }
      },
      // mouseup
      canvasUp (e) {
        console.log('canvasUp');
        this.canvasMoveUse = false;
        // console.log(this.pathArray);
      },
      // mousedown
      canvasDown (e) {
        console.log('canvasDown');
        this.canvasMoveUse = true;
        // client是基于整个页面的坐标
        // offset是cavas距离顶部以及左边的距离
        const canvasX = e.clientX - e.target.parentNode.offsetLeft;
        const canvasY = e.clientY - e.target.parentNode.offsetTop;
        this.setCanvasStyle();
        // 清除子路径
        this.context.beginPath();
        this.context.moveTo(canvasX, canvasY);
        console.log('moveTo', canvasX, canvasY);

        // 当前绘图表面状态
        // const preData = this.context.getImageData(0, 0, 600, 400);
      },
      // 设置绘画配置
      setCanvasStyle () {
        this.context.lineWidth = this.config.lineWidth;
        this.context.shadowBlur = this.config.shadowBlur;
        this.context.shadowColor = this.config.lineColor;
        this.context.strokeStyle = this.config.lineColor;
      },
      connectEvent() {
        /* var me = this;
        var randomNum = Math.floor(Math.random() * 10);
        // this.httpServer = io.connect('http://192.168.175.103:3000');
        console.log(io);
        this.httpServer.emit('login', this.userInfo); */
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .innerDraw
    .wrap
      padding: 0 12px 0 12px
      width: 80%
      canvas
        background-color: #888888
</style>



